import React, { useEffect, useState } from 'react';
import { useRouter } from 'next/router'; // Assuming you're using Next.js
import { useSelector } from 'react-redux';
import { fetchOrderSummary } from '@/app/service/OrderService'; // Assuming the path to the new service

const CheckoutPage = () => {
  const router = useRouter();
  const { orderId } = router.query;  // Assuming you're passing `orderId` in the URL
  const [orderSummary, setOrderSummary] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [shippingAddress, setShippingAddress] = useState('');
  const [billingAddress, setBillingAddress] = useState('');
  const [paymentMethod, setPaymentMethod] = useState('credit-card');
  const [addressError, setAddressError] = useState('');

  const isLoggedIn = useSelector((state) => state.session.user);

  useEffect(() => {
    if (!orderId) return; // Exit if no orderId is available
    const fetchData = async () => {
      try {
        const orderData = await fetchOrderSummary(orderId);
        setOrderSummary(orderData); // Store the order summary
        setLoading(false);
      } catch (err) {
        setLoading(false);
        setError('Failed to load order summary');
      }
    };
    fetchData();
  }, [orderId]);

  const handleSubmit = () => {
    // Validate shipping and billing address before proceeding
    if (!shippingAddress || !billingAddress) {
      setAddressError('Both shipping and billing addresses are required.');
      return;
    }

    setAddressError(''); // Clear any previous error
    // Handle form submission here, like saving the address and proceeding to payment
    console.log({
      shippingAddress,
      billingAddress,
      paymentMethod,
      orderSummary
    });
    // Navigate to payment page or handle payment logic
    router.push('/payment');  // Redirect to the payment page
  };

  if (loading) {
    return <div>Loading...</div>;
  }

  if (error) {
    return <div>{error}</div>;
  }

  if (!orderSummary) {
    return <div>No order found.</div>;
  }

  const { items, totalAmount } = orderSummary;

  return (
    <section className="py-12 sm:py-16">
      <div className="container mx-auto px-4">
        <h2 className="text-3xl font-bold text-gray-900">Order Summary</h2>

        <div className="mt-8">
          {/* Order Items Section */}
          <h3 className="text-2xl font-semibold">Items in your Order:</h3>
          <div className="mt-4">
            {items.map((item) => (
              <div key={item.productId} className="flex justify-between py-2">
                <div className="text-gray-800">{item.name}</div>
                <div className="text-gray-500">{item.quantity} x ${item.price.toFixed(2)}</div>
              </div>
            ))}
          </div>

          <div className="mt-6 border-t pt-4">
            <div className="flex justify-between text-lg font-semibold text-gray-800">
              <div>Total</div>
              <div>${totalAmount.toFixed(2)}</div>
            </div>
          </div>

          {/* Shipping Address Section */}
          <div className="mt-8">
            <h3 className="text-2xl font-semibold">Shipping Address</h3>
            <textarea
              value={shippingAddress}
              onChange={(e) => setShippingAddress(e.target.value)}
              placeholder="Enter shipping address"
              className="w-full mt-2 p-3 border rounded-md"
              rows="4"
            />
          </div>

          {/* Billing Address Section */}
          <div className="mt-8">
            <h3 className="text-2xl font-semibold">Billing Address</h3>
            <textarea
              value={billingAddress}
              onChange={(e) => setBillingAddress(e.target.value)}
              placeholder="Enter billing address"
              className="w-full mt-2 p-3 border rounded-md"
              rows="4"
            />
          </div>

          {/* Address Validation Error */}
          {addressError && (
            <div className="mt-4 text-red-600">{addressError}</div>
          )}

          {/* Payment Method Section */}
          <div className="mt-8">
            <h3 className="text-2xl font-semibold">Payment Method</h3>
            <div className="mt-4">
              <div className="flex items-center">
                <input
                  type="radio"
                  id="credit-card"
                  name="paymentMethod"
                  checked={paymentMethod === 'credit-card'}
                  onChange={() => setPaymentMethod('credit-card')}
                  className="mr-2"
                />
                <label htmlFor="credit-card" className="text-lg">Credit/Debit Card</label>
              </div>
              <div className="flex items-center mt-2">
                <input
                  type="radio"
                  id="paypal"
                  name="paymentMethod"
                  checked={paymentMethod === 'paypal'}
                  onChange={() => setPaymentMethod('paypal')}
                  className="mr-2"
                />
                <label htmlFor="paypal" className="text-lg">PayPal</label>
              </div>
            </div>
          </div>

          {/* Submit Button Section */}
          <div className="mt-8 flex justify-between items-center">
            <button 
              onClick={handleSubmit} 
              className="inline-flex items-center justify-center rounded-md border-2 border-transparent bg-gray-900 px-12 py-3 text-center text-base font-bold text-white transition-all duration-200 ease-in-out focus:shadow hover:bg-gray-800">
              Proceed to Payment
            </button>
            <button 
              onClick={() => router.push('/cart')} 
              className="inline-flex items-center justify-center rounded-md border-2 border-transparent bg-gray-300 px-8 py-3 text-center text-base font-semibold text-gray-700 transition-all duration-200 ease-in-out hover:bg-gray-400">
              Go back to Cart
            </button>
          </div>
        </div>
      </div>
    </section>
  );
};

export default CheckoutPage;
